:root {
  --section-overlay : rgba(89, 90, 109, 0.6);
  --background-overlay: rgba(46, 21, 21, 0.2);

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app {
  width: 100%;
  height: 100vh;
  background-position:center;
  background-size: cover;
}

.overlay {
  width: 100%;
  height: 100vh;
  background-color: var(--background-overlay);
}

.container {
  max-width: 900px;
  margin: auto;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}

/* TOP */ 

.section {
  width: 100%;
  padding: 1rem;
  border-radius: 0.9rem;
  color: white;
}

.section__inputs, .section__temperature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--section-overlay)
}

.section__inputs>input {
  border: 0.8px solid white;
  border-radius: 0.8rem;
  background-color: transparent;
  color: white;
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 200;
}

.section__inputs>input {
 outline: none;
}

.section__inputs>button {
  padding: 0.9em 1.8em;
  border: 2px solid #c7c7da;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  transition: .3s;
  z-index: 1;
  font-family: inherit;
  color: #a8a6b1;
}
.section__inputs>button::before {
  content: '';
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #53535f;
  transition: .5s ease;
  display: block;
  z-index: -1;
 }

.section__inputs>button:hover::before {
    width: 105%;
   
}
.section__inputs>button:hover {
 cursor: pointer;
 color: #957fcf;

}

.icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 190px;
  height: 254px;
  border-radius: 30px;
  box-shadow: 15px 15px 30px #908e97,
             -15px -15px 30px #778086;
}

.icon>h3 {
  font-size: 15px;
  font-weight: 200;
  text-transform: capitalize;
}

.temperature>h1 {
  font-size: 70px;
  
}

